import { Err, Ok, Result } from 'ts-res';
import { Environment, ExternalPackageInfoType } from 'src/types';
import { KameleoonError } from 'src/kameleoonError';
import { JSONType } from 'src/campaignConfiguration';
import { Utilities } from 'src/utilities';
import { URL, UrlParameter, Header, UrlQuery, UrlEventType } from './constants';
import {
  GetClientConfigurationResultType,
  RequesterParamsType,
  TrackDataParamsType,
  TrackExperimentParamsType,
  IExternalRequestDispatcher,
  GetVisitorDataResultType,
} from './types';

export interface IRequester {
  getClientConfiguration: () => Promise<
    Result<GetClientConfigurationResultType, KameleoonError>
  >;
  getRemoteData: (key: string) => Promise<Result<JSONType, KameleoonError>>;
  trackExperiment: ({
    variationId,
    visitorCode,
    experimentId,
    isUnallocated,
    body,
  }: TrackExperimentParamsType) => Promise<Result<boolean, KameleoonError>>;
  trackData: ({
    visitorCode,
    body,
  }: TrackDataParamsType) => Promise<Result<boolean, KameleoonError>>;
}

export class Requester implements IRequester {
  private siteCode: string;
  private environment?: Environment;
  private requestDispatcher: IExternalRequestDispatcher;
  private packageInfo: ExternalPackageInfoType;

  constructor({
    siteCode,
    environment,
    packageInfo,
    requestDispatcher,
  }: RequesterParamsType) {
    this.requestDispatcher = requestDispatcher;
    this.siteCode = siteCode;
    this.environment = environment;
    this.packageInfo = packageInfo;
  }

  public async getClientConfiguration(
    timeStamp?: number,
  ): Promise<Result<GetClientConfigurationResultType, KameleoonError>> {
    const environmentParam = this.environment
      ? UrlParameter.Environment + this.environment
      : '';
    const timeStampParam = timeStamp ? UrlParameter.Ts + timeStamp : '';

    const requestUrl =
      URL.CLIENT_CONFIGURATION +
      UrlQuery.Mobile +
      this.siteCode +
      environmentParam +
      timeStampParam;

    try {
      const response = await this.requestDispatcher.getClientConfiguration(
        requestUrl,
      );

      return Ok(response as GetClientConfigurationResultType);
    } catch (error) {
      return Err(error as KameleoonError);
    }
  }

  public async getRemoteData(
    key: string,
  ): Promise<Result<JSONType, KameleoonError>> {
    const requestUrl: string =
      URL.DATA_MAP + this.siteCode + UrlParameter.Key + encodeURI(key);

    try {
      const response = await this.requestDispatcher.getRemoteData(requestUrl);
      return Ok(response);
    } catch (error) {
      return Err(error as KameleoonError);
    }
  }

  public async getVisitorData(
    visitorCode: string,
  ): Promise<Result<GetVisitorDataResultType, KameleoonError>> {
    const requestUrl: string =
      URL.VISIT_DATA +
      this.siteCode +
      UrlParameter.VisitorCode +
      visitorCode +
      UrlParameter.MaxNumberPreviousVisits +
      1 +
      UrlParameter.CustomData +
      true +
      UrlParameter.CurrentVisit +
      true;

    try {
      const response = await this.requestDispatcher.getRemoteData(requestUrl);
      return Ok(response as GetVisitorDataResultType);
    } catch (error) {
      return Err(error as KameleoonError);
    }
  }

  public async trackExperiment({
    variationId,
    visitorCode,
    experimentId,
    isUnallocated,
    body,
    userAgent,
  }: TrackExperimentParamsType): Promise<Result<boolean, KameleoonError>> {
    const url = this.getTrackingUrl(visitorCode);

    const unallocatedAddition = !body
      ? UrlEventType.Activity + UrlParameter.Nonce + Utilities.getNonce()
      : '';
    const bodyAddition = isUnallocated
      ? unallocatedAddition
      : UrlEventType.Experiment +
        UrlParameter.Id +
        experimentId +
        UrlParameter.VariationId +
        variationId +
        UrlParameter.Nonce +
        Utilities.getNonce();
    const extendedBody = bodyAddition ? body + '\n' + bodyAddition : body;

    const headers = userAgent
      ? {
          [Header.UserAgent]: userAgent,
        }
      : undefined;

    try {
      const response = await this.requestDispatcher.track({
        url,
        headers,
        body: extendedBody,
      });

      return Ok(response);
    } catch (error) {
      return Err(error as KameleoonError);
    }
  }

  public async trackData({
    visitorCode,
    body,
    userAgent,
  }: TrackDataParamsType): Promise<Result<boolean, KameleoonError>> {
    const url = this.getTrackingUrl(visitorCode);

    const headers = userAgent
      ? {
          [Header.UserAgent]: userAgent,
        }
      : undefined;

    const requestBody =
      body || UrlEventType.Activity + UrlParameter.Nonce + Utilities.getNonce();

    try {
      const response = await this.requestDispatcher.track({
        url,
        headers,
        body: requestBody,
      });

      return Ok(response);
    } catch (error) {
      return Err(error as KameleoonError);
    }
  }

  private getTrackingUrl(visitorCode: string): string {
    const { type, version } = this.packageInfo;

    return (
      URL.VISIT_EVENT +
      this.siteCode +
      UrlParameter.VisitorCode +
      visitorCode +
      UrlParameter.SdkName +
      type.toLowerCase() +
      UrlParameter.SdkVersion +
      version
    );
  }
}
